@import url("https://fonts.cdnfonts.com/css/euclid-circular-a");

body {
  font-family: "Euclid Circular A", sans-serif;
  overflow-x: hidden;
  /* cursor: none; */
}

html {
  scroll-behavior: smooth;
}

*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.ui-card {
  background: radial-gradient(#111 50%, #000 100%);
}

.ui-card .image {
  transform: translate(-1px);
  transition: all 0.3s ease-in;
}
.ui-card:hover .image {
  transform: scale(1.1) translateY(0px);
  opacity: 0.3;
}
.ui-card .description {
  position: absolute;
  top: 60px;
  left: 0;
  padding: 15px;
  width: 100%;
  text-align: center;
  color: #fff;
}
.ui-card .description .textcircle {
  font-weight: 700;
  margin: 0 0 20px;
  transform: translateY(30px);
  opacity: 0;
  transition: all 0.3s ease-out;
}
.ui-card .description .textcirclesm {
  transform: translateY(50px);
  opacity: 0;
  transition: all 0.3s ease-out 0.2s;
}
.ui-card:hover .description .textcircle {
  opacity: 1;
  transform: translateY(0px);
}
.ui-card:hover .description .textcirclesm {
  opacity: 1;
  transform: translateY(0px);
}

.ReactCollapse--collapse {
  transition: height 300ms;
}

.border-gradient-orange:hover {
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  background: linear-gradient(#000, #000) padding-box,
    linear-gradient(to right, #f05225, #eea820) border-box;
  border-left: 0;
  border-right: 0;
  border-top: 0;
  border-radius: 2px;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.border-gradient-box-9 {
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  background: linear-gradient(#000, #000) padding-box,
    linear-gradient(302.76deg, #d400a5 54.81%, #1f7ecb 85.95%, #2ed6e3 114.01%)
      border-box;
}

/* UI-UX DESIGING  */
@import url("https://fonts.cdnfonts.com/css/euclid-circular-a");

.box {
  animation: loader 1.25s infinite;
}

@keyframes loader {
  0% {
    rotate: 0deg;
  }

  100% {
    rotate: 180deg;
  }
}

@media only screen and (max-width: 768px) {
  .box {
    visibility: hidden;
  }
}

@media only screen and (max-width: 768px) {
  .squer {
    visibility: hidden;
  }
}

.swiper-button-next {
  visibility: hidden;
}

.swiper-button-prev {
  visibility: hidden;
}

.scrolling-wrapper {
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;

  .card {
    display: inline-block;
  }
}

/* style image*/
.style-image {
  list-style-image: url("/public/img/Yes.png");
  font-size: 14px;
  padding: 5px;
}

/* Contact Animation */
.contact-square-1 {
  height: 10rem;
  width: 10rem;
  background: linear-gradient(130.35deg, #ef32d9 -4.08%, #3f5efb 98.72%);
  border-radius: 20%;
  animation: rotate 5s linear infinite;
}

.contact-square-2 {
  height: 10rem;
  width: 10rem;
  background: linear-gradient(130.35deg, #ef32d9 -4.08%, #3f5efb 98.72%);
  border-radius: 20%;
  animation: rotate-1 5s linear infinite;
}

/* About Animation */
.square {
  animation: rotate 10s linear infinite;
}

.square-2 {
  animation: rotate-1 10s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-1 {
  from {
    transform: rotate(360deg);
  }

  to {
    transform: rotate(0deg);
  }
}

.textnew {
  transform: translate(-50%, -50%);
  color: black;
  letter-spacing: 2px;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
}

.text-gradient {
  background: -webkit-linear-gradient(#f05225, #eea820);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-gradient-2 {
  background: -webkit-linear-gradient(#0da1da, #5db98e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-gradient-3 {
  /* background: -webkit-linear-linear-gradient(#0038ff, #3a86ff, #6dccff); */
  background: -webkit-linear-gradient(#0038ff, #3a86ff, #6dccff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}


.glassmorphism {
  background: #3a3a3a33;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.glassmorphism-2 {
  background: rgba(255, 255, 255, 0.106);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.glassmorphism-3 {
  background: rgba(170, 170, 170, 0.2);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.glassmorphism-4 {
  background: rgba(223, 223, 223, 0.313);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.circle {
  position: absolute;
  width: 600.48px;
  height: 600.48px;
  filter: blur(100.5px);
  border-radius: 500px;
}

/* code design */
.title {
  height: 50px;
  overflow: hidden;
}

.title-wrapper {
  height: 100%;
  animation: move 5s ease-in-out infinite alternate;
}

.title-item {
  height: 50px;
  display: flex;
  align-items: center;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }

  50% {
    transform: translateY(-100px);
  }
}

/* Map animation */
.map-ring {
  animation: mapanimation 1.5s ease-in-out infinite alternate;
}

@keyframes mapanimation {
  25% {
    transform: translateY(-25px);
  }

  50% {
    transform: translateY(0);
  }
}

.mask-circle {
  -webkit-mask-image: radial-gradient(circle, black 60%, rgba(0, 0, 0, 0) 70%);
  mask-image: radial-gradient(circle, black 60%, rgba(0, 0, 0, 0) 70%);
}

.noscrollbar::-webkit-scrollbar {
  display: none;
}

.carousel::-webkit-scrollbar {
  display: none;
}

.orange:hover {
  position: relative;
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(#f05225, #eea820);
  font-family: Roboto;
  overflow: hidden;

  &:before,
  &:after {
    content: "";
    position: absolute;
    left: 50%;
    min-width: 300vw;
    min-height: 300vw;
    background-color: #fcfff5;
    animation-name: spin;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }

  &:before {
    bottom: 15vh;
    border-radius: 45%;
    animation-duration: 6s;
  }

  &:after {
    bottom: 12vh;
    opacity: 0.5;
    border-radius: 47%;
    animation-duration: 6s;
  }
}

@keyframes spin {
  0% {
    transform: translate(-50%, 0) rotateZ(0deg);
  }

  50% {
    transform: translate(-100%, -2%) rotateZ(180deg);
  }

  100% {
    transform: translate(-100%, 0%) rotateZ(360deg);
  }
}

